import { Component, Input, OnInit, Output,EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormBuilder, FormGroup, FormArray, FormsModule, ReactiveFormsModule, FormControl,Validators } from '@angular/forms';
import { NgxSliderModule,Options } from '@angular-slider/ngx-slider';
import {MatCheckboxModule} from '@angular/material/checkbox';
import { distinctUntilChanged } from 'rxjs/operators';
import moment from 'moment-jalaali';
@Component({
  selector: 'app-responsive-table',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, MatCheckboxModule,FormsModule,NgxSliderModule],
  templateUrl: './responsive-table.component.html',
  styleUrls: ['./responsive-table.component.css']
})
export class ResponsiveTableComponent implements OnInit {
  @Input() data: any = {};
  @Input() id:string='';
  @Input() expireDate:string='';
  @Input() formGroup!: FormGroup;
  @Output() outputArrayChange = new EventEmitter<{ [x: number]: any; }>();
  get isRequiredInput(){
    return this.data.isRequiredInput
  }
  output: any = {
    column:[],isReadyToSend:false,link:'',linkCaption:''
  };
  get currentJalaliDate():string{
    moment.loadPersian({ usePersianDigits: false });
    const jalaliDate: string = moment().format('jYYYY/jMM/jDD');
    return (jalaliDate);
  }
  get expiredDate():string {
    return localStorage.getItem('expiredDate') || "";
  }
  private notExpiredAndLinkIsId(output:any):boolean{
    return this.currentJalaliDate<this.expiredDate && output.link===this.id
  }
  get savedLocalStorageValue():any{
    // const storedData = localStorage.getItem(this.id) || "";
    // if(storedData!="") { 
    //   const output = JSON.parse(storedData);
    //   if(this.notExpiredAndLinkIsId(output)) {
    //     return output;
    //   }
    // }
    return {};
  } 
  get formattedData() {
    if (this.data && this.data.row) {
      return {
        rows: this.data.row.map((row: any) =>
          row.column.map((col: any) => col)
        ),
      };
    }
    return null;
  }

  constructor(private fb: FormBuilder) {}
  get rowArray(): FormArray {
    return this.formGroup.get('rows') as FormArray;
  }
  get row0():FormGroup {
    return this.rowArray.at(0) as FormGroup ;
  }  
  get caption():string {
    return this.getColumns(this.row0).at(0).get('value')?.value
  } 
  ngOnInit(): void {
    this.initForm();
    this.rangeMaxValue
    this.output.link =this.id,
    this.output.linkCaption =`${this.caption} - ${this.id.includes('Diviation')?' ':'  '}`
    this.outputArrayChange.emit(this.output);
    this.row0.get('isCheckedValue')?.valueChanges
    .pipe(distinctUntilChanged()) // Only trigger when value actually changes
    .subscribe((newValue) => {
      this.row0.get('isCheckedValue')?.setValue(newValue, { emitEvent: false });
      this.updateOutputArray();
      // Convert the outputs array to a JSON string
      const outputsJson = JSON.stringify(this.output);
      // Save the JSON string to local storage
      localStorage.setItem(this.id, outputsJson);
      this.outputArrayChange.emit(this.output);
    });
    // Initial population of outputArray
    //this.updateOutputArray();    
    for(let i=0; i< this.rows.length;i++){
      if(this.data.hasRangeLinearScale ) {
        if(this.rows.at(i).get('isCheckedValue')?.value && this.rows.at(i).get('isCheckedRange')?.value){
          this.rows.at(i).get('isCheckedValue')?.enable();
        } else {
         this.rows.at(i).get('isCheckedValue')?.disable();
        }
      } else {
        this.rows.at(i).get('isCheckedValue')?.enable();
      }
    }
  }
  // updateOutputArray(): void {
  //   const rows = this.formGroup.get('rows') as FormArray;
  
  //   rows.controls.forEach((rowControl, rowIndex) => {
  //     const row = rowControl as FormGroup;
  //     const columns = row.get('columns') as FormArray;
  
  //     // Extract column values for this row
  //     const columnValues = columns.controls.map((columnControl) => {
  //       const column = columnControl as FormGroup;
  //       return column.get('value')?.value; // Get the 'value' control
  //     });
  
  //   });
  // }
  updateOutputArray(): void {
    const rows = this.formGroup.get('rows') as FormArray;
    rows.controls.forEach((rowControl, rowIndex) => {
      const row = rowControl as FormGroup;
      const columns = row.get('columns') as FormArray;
  
      // Extract column values for this row
      const columnValues = columns.controls.map((columnControl) => {
        const column = columnControl as FormGroup;
        return {value:column.get('value')?.value,min:column.get('min')?.value,max:column.get('max')?.value}; 
      });
      this.output.isReadyToSend = this.row0.get('isCheckedValue')?.value
      this.output.column = [...columnValues];
      this.output.link =this.id,
      this.output.linkCaption = `${this.caption} - ${this.id.includes('Diviation')?' ':'  '}`
    });
    //const dictionary = this.output.link ? { [this.output.link]: this.output } : {};
    //console.log(dictionary);
  }
  get rows(): FormArray<FormGroup> {
    return this.formGroup.get('rows') as FormArray<FormGroup>;
  }
  getCols(index: number): FormArray<FormGroup> {
    const group = this.rows.at(index).get("columns");
    return group as FormArray<FormGroup>;
  }
  getColumns(row: FormGroup): FormArray<FormGroup> {
    return row.get('columns') as FormArray<FormGroup>;
  }
  initForm() {
    const _formattedData = this.formattedData;
    if (_formattedData) {
      this.formGroup = this.fb.group({
        rows: this.fb.array(
          _formattedData.rows.map((row: any, rowIndex: number) =>
            this.fb.group({
              isCheckedRange: new FormControl({ value: this.savedLocalStorageValue?  this.savedLocalStorageValue.isReadyToSend : false, disabled:  false }),
              isCheckedValue: new FormControl({ value: this.savedLocalStorageValue?  this.savedLocalStorageValue.isReadyToSend : false, disabled:  false }),
              columns: this.fb.array(
                row.map((col: any, colIndex: number) =>
                  this.fb.group({
                    min: this.fb.control(this.savedLocalStorageValue &&  this.savedLocalStorageValue.isReadyToSend?  this.savedLocalStorageValue.column[colIndex].min :col.min),
                    max: this.fb.control(this.savedLocalStorageValue &&  this.savedLocalStorageValue.isReadyToSend?  this.savedLocalStorageValue.column[colIndex].max :col.max),
                    value: this.fb.control(this.savedLocalStorageValue &&  this.savedLocalStorageValue.isReadyToSend?  this.savedLocalStorageValue.column[colIndex].value :col.value),
                    defVal: this.fb.control(col.defVal),
                    valType:this.fb.control(col.valType)
                  })
                )
              ),
            })
          )
        ),
      });
    }
  }
  
  /**
   * Helper function to return validators for the value field
   */
  private getValidators(col: any): any {
    const validators = [];
  
    // Check if value is required
    if (this.isRequiredInput) {
      validators.push(Validators.required);
    }
  
    // Add minimum and maximum constraints
    if (typeof col.min === 'number') {
      validators.push(Validators.min(col.min));
    }
    if (typeof col.max === 'number') {
      validators.push(Validators.max(col.max));
    }
  
    // Add float number validation
    validators.push(this.floatNumberValidator);
  
    return validators;
  }
  
  /**
   * Custom validator to check if the value is a valid float number
   */
  private floatNumberValidator(control: any) {
    const value = control.value;
    if (!/^[-+]?[0-9]*\.?[0-9]+$/.test(value)) {
      return { notFloat: true };
    }
    return { notFloat: false };
  }
  
  getRowControls(row: any): FormArray {
    return row as FormArray;
  }
  getMin(rowIndex: number, colIndex: number): number {
    return this.data.row[rowIndex].column[colIndex]?.min || 0;
  }

  getMax(rowIndex: number, colIndex: number): number {
    return this.data.row[rowIndex].column[colIndex]?.max || 100;
  }
  getValue(rowIndex: number, colIndex: number): number {
    return this.data.row[rowIndex].column[colIndex]?.value || 0;
  }

  getColumnControlName(rowIndex: number, colIndex: number): string {
    return `rows_${rowIndex}_${colIndex}`;
  }

  isNumeric(objectValue: any): boolean {
    if (!objectValue || !objectValue.value || typeof objectValue.value.value !== 'string') {
      return false; // Return false if objectValue or its properties are invalid
    }
  
    const value = objectValue.value.value.trim(); // Access and trim the value
  
    // Check if the value is numeric or an empty string
    return value === "" || !isNaN(Number(value));
  }
  formatLabel(value: number): string {
    if (value >= 1000) {
      return Math.round(value / 1000) + 'k';
    }

    return `${value}`;
  }
  getFormControlName(control: FormControl, parent: FormGroup): string | null {
    // Find the control's name within the parent group
    for (const name in parent.controls) {
      if (parent.controls[name] === control) {
        return name; // Return the name if the control matches
      }
    }
    return null; // Return null if not found
  }
  onInputChange(event: Event, rowIndex: number, colIndex: number): void {
    const inputElement = event.target as HTMLInputElement;
    const newValue = parseFloat(inputElement.value);
    const cell = this.getCols(rowIndex).at(colIndex) as FormGroup;
  
    // Update the 'value' FormControl in the FormGroup
    cell.get('value')?.setValue(newValue);
  }  
  allowOnlyNumeric(event: KeyboardEvent): boolean {
    const allowedKeys = ['Backspace', 'Tab', 'ArrowLeft', 'ArrowRight', 'Delete'];
    const inputChar = event.key;
  
    // Allow special keys
    if (allowedKeys.includes(inputChar)) {
      return true;
    }
  
    // Allow numbers, one decimal point, and a negative sign
    const regex = /^[0-9\-\.]$/;
    if (!regex.test(inputChar)) {
      event.preventDefault();
      return false;
    }
  
    // Ensure only one decimal point
    const inputElement = event.target as HTMLInputElement;
    if (inputChar === '.' && inputElement.value.includes('.')) {
      event.preventDefault();
      return false;
    }
  
    // Ensure only one negative sign at the start
    if (inputChar === '-' && inputElement.selectionStart !== 0) {
      event.preventDefault();
      return false;
    }
  
    return true;
  }
  
  validateFloat(event: Event): void {
    const inputElement = event.target as HTMLInputElement;
    const regex = /^-?\d*\.?\d*$/;
  
    // If the value is not a valid float, reset it
    if (!regex.test(inputElement.value)) {
      inputElement.value = inputElement.value.replace(/[^0-9.\-]/g, '');
      if (inputElement.value.split('.').length > 2) {
        inputElement.value = inputElement.value.replace(/\.+$/, '');
      }
    }
  }
 
  // Range Slider values
  rangeMinValue: number = 20;
  rangeMaxValue: number = 80;

  // Value Slider value
  valueSliderValue: number = (this.rangeMinValue + this.rangeMaxValue) / 2;

  // Range Slider options
  getRangeSliderOptions(rowIndex:number,colIndex:number,isVertical:boolean): Options {
    return{
      floor: this.data.row[rowIndex].column[colIndex].min,
      ceil: this.data.row[rowIndex].column[colIndex].max,
      vertical: isVertical,
      step: this.data.row[rowIndex].step,
      showTicks: true,
      translate: (value: number): string => this.convertToFarsiDigits(value)
    }
  }
  // getRangeSliderOptionsHorz: Options = {
  //   floor: 0,
  //   ceil: 100,
  //   step: 1
  // };
  // Dynamic slider options for Value Slider
  getValueSliderOptions(rowIndex: number, colIndex: number,isVertical:boolean): Options {
    const cell = this.getCols(rowIndex).at(colIndex);
    const enableCheckedValue = this.getIsCheckedValue(rowIndex);
    return {
      disabled:enableCheckedValue,
      floor: isVertical? cell.get('min')?.value:this.data.row[rowIndex].column[colIndex].min,
      ceil:  isVertical? cell.get('max')?.value:this.data.row[rowIndex].column[colIndex].max,
      vertical: isVertical,
      step: this.data.row[rowIndex].step,
      showTicks: true,
      translate: (value: number): string => this.convertToFarsiDigits(value)
    };
  }
  getValueSliderOptionsHorz(rowIndex: number, colIndex: number): Options {
    const cell = this.getCols(rowIndex).at(colIndex);
    const enableCheckedValue = this.getIsCheckedValue(rowIndex);
    return {
      disabled:enableCheckedValue,
      floor: cell.get('min')?.value,
      ceil: cell.get('max')?.value,      
      step: 1,
      showTicks: true,
      translate: (value: number): string => this.convertToFarsiDigits(value)
    };
  }
  getRowForCheck(rowIndex: number):{isCheckedRange:boolean,isCheckedValue:boolean}{
    const _range = this.rows.at(rowIndex).get("isCheckedRange")?.value
    const _value = this.rows.at(rowIndex).get("isCheckedValue")?.value
    return {isCheckedRange:_range,isCheckedValue:_value}
  
  }
  // Event handlers for sliders
  onRangeMinChange(value: number, rowIndex: number, colIndex: number): void {
    const cell = this.getCols(rowIndex).at(colIndex);
    cell.get('min')?.setValue(value);
  }

  onRangeMaxChange(value: number, rowIndex: number, colIndex: number): void {
    const cell = this.getCols(rowIndex).at(colIndex);
    cell.get('max')?.setValue(value);
  }

  onValueSliderChange(value: number, rowIndex: number, colIndex: number): void {
    const cell = this.getCols(rowIndex).at(colIndex);
    cell.get('value')?.setValue(value);
  }
  setIsCheckedRange(rowIndex:number) {
      const  _isChecked = this.rows.at(rowIndex).get("isCheckedRange")?.value
      if(!_isChecked) {
        this.rows.at(rowIndex).get("isCheckedValue")?.disable()
        this.rows.at(rowIndex).get("isCheckedValue")?.setValue(false);
      } else {
        this.rows.at(rowIndex).get("isCheckedValue")?.enable()
      }
  }  
  setIsCheckedValue(event:any,rowIndex:number) {
    const  _isChecked = this.rows.at(rowIndex).get("isCheckedValue")?.value
    if(_isChecked){
      this.getCols(rowIndex).at(1).get('value')?.disable();
    } else {
      this.getCols(rowIndex).at(1).get('value')?.enable();
    }
    // if(_isChecked && rowIndex+1 < this.rows.length) {
    //   this.rows.at(rowIndex+1).get("isCheckedRange")?.enable();
    // } else {
    //   this.rows.at(rowIndex+1).get("isCheckedRange")?.disable();
    // }
  }
  getValType(rowIndex:number,colIndex:number):string{
    return this.data.row[rowIndex].column[colIndex].valType;
  }
  getIsCheckedValue(rowIndex:number):boolean{
    return this.rows.at(rowIndex).get('isCheckedValue')?.value //this.data.row[rowIndex].isCheckedValue;
  } 
 convertToFarsiDigits(value: number | string): string {
    const farsiDigits = ['', '', '', '', '', '', '', '', '', ''];
    return value
      .toString()
      .split('')
      .map((digit) => (/\d/.test(digit) ? farsiDigits[+digit] : digit))
      .join('');
  }
  
}
