import { Component } from '@angular/core';
import { CommonModule } from '@angular/common'
import { FormBuilder, FormGroup, ReactiveFormsModule,Validators } from '@angular/forms';
import { v4 as uuidv4 } from 'uuid';
import { GooglSheetService } from '../googl-sheet.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-addnewuser',
  standalone: true,
  imports: [ReactiveFormsModule,CommonModule], // Import ReactiveFormsModule
  templateUrl: './addnewuser.component.html',
  styleUrl: './addnewuser.component.css',
})
export class AddnewuserComponent {
  userForm: FormGroup; // Define the FormGroup
  constructor(private fb: FormBuilder,private googleSheetService: GooglSheetService,private router: Router) {
    // Initialize the form with form controls
    this.userForm = this.fb.group({
      firstName: [''], // Control for first name
      lastName: [''], // Control for last name
      mobileNumber: ['',[Validators.required,  Validators.pattern(/^[0-9-]{11}$/)]],
      educationLevel: ['',[ Validators.required]],
      activityType: ['',[ Validators.required]],
      position: ['',[ Validators.required]]
    });
  }
// Helper function to get the userId
  getUserId(mobileNumberValue: string | null, isMobileNumberValid: boolean): string {
    return mobileNumberValue && isMobileNumberValid ? `mobile${mobileNumberValue}` : uuidv4();
  }
  
  submitForm(): void {
    this.canGoToTheNextStepConditionFail = this.userForm.get('position')?.value==='' || this.userForm.get('activityType')?.value==='' || this.userForm.get('educationLevel')?.value==='';
    if(this.canGoToTheNextStepConditionFail){ return ;}
    // Get the mobile number value and validity
    const mobileNumberControl = this.userForm.get('mobileNumber');
    const mobileNumberValue = mobileNumberControl?.value;
    const isMobileNumberValid = mobileNumberControl?.valid || false;
    // Determine the userId
    const userId = this.getUserId(mobileNumberValue, isMobileNumberValid);
    const userType='nonExpert';
    const user = {
      userInfo:
        {
          timestamp: new Date().toLocaleString(),
          userId: this.getUserId(mobileNumberValue, isMobileNumberValid),
          firstName:this.userForm.get('firstName')?.value || '',
          lastName:this.userForm.get('lastName')?.value || '',
          educationLevel:this.userForm.get('educationLevel')?.value,
          activityType:this.userForm.get('activityType')?.value,
          position:this.userForm.get('position')?.value
        }
    }
    try {
      this.googleSheetService.postData(user,'https://script.google.com/macros/s/AKfycbxEgr1R-LUBr2NrCvl64VbHNx2RG_7_zWoMMBsJvW7EcaRLmZBkpJaKz5oqUK6ij7-Aag/exec');
      this.router.navigate([''], { queryParams: { userId,userType } });
    } catch (error: any) {
      console.log(`problem to send:${user}`);
    }    
  }
  canGoToTheNextStepConditionFail:boolean=false;
  openDropdown() {
    const selectElement = document.querySelector('select[formControlName="activityType"]') as HTMLSelectElement;
    if (selectElement) {
      // Focus the select element
      selectElement.focus();
  
      // Create and dispatch a mousedown event to open the dropdown
      const event = new MouseEvent('mousedown', {
        bubbles: true,
        cancelable: true,
        view: window
      });
      selectElement.dispatchEvent(event);
    }
  }
}