import { Component } from '@angular/core';
import { QrCodeService } from '../qr-code.service';
import { CommonModule } from '@angular/common';
@Component({
  selector: 'qr-code',
  standalone: true,
  imports: [CommonModule],
  template: `
    <div>
      <h1>QR Code Generator</h1>
      <img *ngIf="qrCodeDataUrl" [src]="qrCodeDataUrl" alt="QR Code" />
      <div *ngIf="qrCodeSvg" [innerHTML]="qrCodeSvg">test</div>
      <svg viewBox="0 0 200 300" shape-rendering="crispEdges"><path fill="#ffffff" d="M0 0h41v41H0z"/><path stroke="#000000" d="M4 4.5h7m2 0h1m3 0h1m1 0h1m2 0h6m2 0h7M4 5.5h1m5 0h1m1 0h1m2 0h2m1 0h6m1 0h1m1 0h2m1 0h1m5 0h1M4 6.5h1m1 0h3m1 0h1m2 0h1m1 0h4m3 0h3m1 0h1m1 0h1m1 0h1m1 0h3m1 0h1M4 7.5h1m1 0h3m1 0h1m2 0h1m3 0h4m2 0h1m3 0h1m2 0h1m1 0h3m1 0h1M4 8.5h1m1 0h3m1 0h1m1 0h7m1 0h2m1 0h1m1 0h3m2 0h1m1 0h3m1 0h1M4 9.5h1m5 0h1m2 0h3m1 0h1m1 0h2m1 0h4m1 0h2m1 0h1m5 0h1M44m2 0h1m1 0h1m1 0h3m1 0h2m1 0h6M12 29.5h1m4 0h1m2 0h2m2 0h2m1 0h2m3 0h2m1 0h2M4 30.5h7m2 0h2m1 0h3m3 0h1m1 0h1m2 0h2m1 0h1m1 0h1m1 0h3M4 31.5h1m5 0h1m4 0h1m3 0h1m1 0h6m1 0h1m3 0h1m2 0h1M4 32.5h1m1 0h3m1 0h1m1 0h1m1 0h1m1 0h1m1 0h3m1 0h1m1 0h2m2 0h6m2 0h1M4 33.5h1m1 0h3m1 0h1m2 0h1m1 0h1m1 0h4m3 0h1m1 0h1m1 0h1m3 0h2m2 0h1M4 34.5h1m1 0h3m1 0h1m1 0h1m1 0h1m1 0h6m4 0h1m1 0h2m2 0h3m1 0h1M4 35.5h1m5 0h1m2 0h2m2 0h1m2 0h3m2 0h5m2 0h1m2 0h1M4 36.5h7m1 0h3m1 0h4m1 0h1m3 0h1m1 0h2m3 0h2m1 0h2"/></svg>
    </div>
  `,
  styleUrl: './app-qr-code.component.css'
})
export class AppQrCodeComponent {
  qrCodeDataUrl: string | null = null;
  qrCodeSvg: string | null = null;
  constructor(private qrCodeService: QrCodeService) {}
  ngOnInit() {
    const url = 'https://psepah.github.io/yarExpertDataPublish/#/newuser'; 

    // Generate QR Code as a Data URL
    this.qrCodeService.generateQrCodeAsDataURL(url).then((dataUrl) => {
      this.qrCodeDataUrl = dataUrl;
    });
    this.qrCodeService.saveQrCodeAsSvg(url);

    // Generate QR Code as an SVG
    this.qrCodeService.generateQrCodeAsString(url).then((svgString) => {
      this.qrCodeSvg = svgString;
    });
  }
}
