import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import {MatCardModule} from '@angular/material/card';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { GooglSheetService } from '../googl-sheet.service'
import { JalaliDatePipe } from '../shared/jalali-date.pipe' ;
import moment from 'moment-jalaali';
@Component({
  selector: 'app-see-questions',
  standalone: true,
  imports: [MatProgressSpinnerModule,MatCardModule,CommonModule,JalaliDatePipe],
  templateUrl: './see-questions.component.html',
  styleUrl: './see-questions.component.css'
})
export class SeeQuestionsComponent {
  apiUrl = "YOUR_GOOGLE_APPS_SCRIPT_DEPLOYMENT_URL";
  questions: { timestamp: string, question: string, new: boolean }[] = [];
  loading = false;
  lastFetchedTime: string | null = null;

  constructor(private googleSheetService: GooglSheetService) {}

  ngOnInit() {
    this.fetchQuestions();
    setInterval(() => this.fetchQuestions(), 10000);
  }

  async fetchQuestions() {
    this.loading = true;
  
    try {
      const data = await this.googleSheetService.fetchData('https://script.google.com/macros/s/AKfycbxACL2Ij0ysY0MjFEAs96LvPPx462jn3VLGDccafXmN78I9L7Xv6qyLcU-TUfj77TI6DA/exec');
  
      // Convert the fetched data into a Set for easy lookup
      const fetchedTimestamps = new Set(data.map((row: { timestamp: string }) => row.timestamp));
  
      // Remove questions that no longer exist in the fetched data
      this.questions = this.questions.filter(q => fetchedTimestamps.has(q.timestamp));
  
      // Add new questions that are not in the existing list
      const newQuestions = data
        .filter(row => !this.questions.some(q => q.timestamp === row.timestamp)) // Only add truly new ones
        .map(row => ({
          timestamp: row.timestamp,
          question: row.question,
          new: true, // Mark all newly added items as "new"
        }));
  
      // Merge updated list (keeping previous questions that still exist)
      this.questions = [...this.questions, ...newQuestions];
  
      this.loading = false;
    } catch (error: any) {
      console.error("Error fetching data:", error);
      this.loading = false;
    }
  }
  
  markAsRead(q: any) {
    q.new = false; // Mark question as read
  
    // Get viewed questions from localStorage
    const viewedQuestions = new Set(JSON.parse(localStorage.getItem('viewedQuestions') || '[]'));
    viewedQuestions.add(q.timestamp);
  
    // Save back to localStorage
    localStorage.setItem('viewedQuestions', JSON.stringify([...viewedQuestions]));
  } 
}
