import { Component } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators, ReactiveFormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatSelectModule } from '@angular/material/select';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatTableModule } from '@angular/material/table';
import { MatCardModule } from '@angular/material/card';
import { MatButtonModule } from '@angular/material/button';
import { FarsiDigitPipe } from '../shared/farsi-digit.pipe' ;
import { CommonModule } from '@angular/common';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { GooglSheetService } from '../googl-sheet.service';
import { MatDialog } from '@angular/material/dialog';
import { DialogMessageComponent } from '../shared/dialog-message/dialog-message.component';
@Component({
  selector: 'app-order-form',
  standalone: true,
  templateUrl: './order-form.component.html',
  styleUrls: ['./order-form.component.css'],
  imports: [
    ReactiveFormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatCheckboxModule,
    MatTableModule,
    MatCardModule,
    MatButtonModule,
    FarsiDigitPipe,
    CommonModule,
    MatProgressSpinnerModule 
  ]
})
export class OrderFormComponent {
  isLoading = false;
  orderForm: FormGroup;
  displayedColumns = ['select', 'header1', 'header2'];
  displayedColumns2 = ['select', 'header1'];
  dataSource = [
    { text: '     ',priceFor1:500,priceAll:2500 },
    { text: '       ',priceFor1:500,priceAll:2500 },
    { text: '   ',priceFor1:500,priceAll:2500 },
    { text: '   ' ,priceFor1:500,priceAll:2500 },
    { text: ' ',priceFor1:1000,priceAll:3500  },
    { text: '  ',priceFor1:3000,priceAll:12000  },
    { text: '     ',priceFor1:5000,priceAll:0  }
  ];
  dataSource2 = [
    { text: '         ',priceFor1:500 },
    { text: '         ',priceFor1:500 },
    { text: '     ',priceFor1:500 },
    { text: '       ' ,priceFor1:2000 }
  ];
  constructor(private fb: FormBuilder, private googlSheetService:GooglSheetService,private dialog: MatDialog) {
    this.orderForm = this.fb.group({
      name: ['', Validators.required],
      family: ['', Validators.required],
      mobileNo: ['', [Validators.required,  Validators.pattern(/^[0-9-]{11}$/)]],
      organization: [''],
      responsible: [''],
      ceo: [''],
      numberOfStaff: [''],
      organizationType: [''],
      activitySegment: [''],
      selectedRows: this.fb.array(this.dataSource.map((item) => this.fb.group({
        text:[item.text],
        value10000: [false],
        value30000: [false]
      }))),
      selectedRowsOther: this.fb.array(this.dataSource2.map((item) => this.fb.group({
        text:[item.text],
        value10000: [false]
      })))
    });
  }

  get selectedRows(): FormArray {
    return this.orderForm.get('selectedRows') as FormArray;
  }
  get selectedRowsOther(): FormArray {
    return this.orderForm.get('selectedRowsOther') as FormArray;
  }
  getRowFormGroup(index: number): FormGroup {
    return this.selectedRows.at(index) as FormGroup;
  }
  getOtherRowFormGroup(index: number): FormGroup {
    return this.selectedRowsOther.at(index) as FormGroup;
  }
  alertInvalidSubmit:boolean=false
  submitForm() {
    if(this.orderForm.valid){
      this.alertInvalidSubmit = true
      this.sendOrder();
    }
  }
  async sendOrder() {
    try {
      this.isLoading=true;
      //const customerOrder= 
      await this.googlSheetService.postData(this.orderForm.value,'https://script.google.com/macros/s/AKfycbw_nGj0i52RvKW5tEaJYLb9irxSd1ocoiNndeaz01q-vFWz9I-tZMmm9CfoPVb4gpEiiA/exec');
      this.dialog.open(DialogMessageComponent, {
        data: { message:'     ', isError:false },
        width: '300px',
      });
      this.orderForm.reset();
      this.isLoading=false;
  
    } catch (error: any) {
      this.dialog.open(DialogMessageComponent, {
        data: { message:'     .     !', isError:true },
        width: '300px'
      });
      this.isLoading=false;
    }
  }
}
