import { Injectable } from '@angular/core';
import * as QRCode from 'qrcode';

@Injectable({
  providedIn: 'root'
})
export class QrCodeService {

  constructor() { }
  generateQrCodeAsDataURL(url: string): Promise<string> {
    return QRCode.toDataURL(url);
  }

  // Generate QR Code as a string for rendering in HTML
  generateQrCodeAsString(url: string): Promise<string> {
    return QRCode.toString(url, { type: 'svg' });
  }  
  async saveQrCodeAsSvg(url: string, fileName: string = 'qrcode.svg'): Promise<void> {
    const svgString = await this.generateQrCodeAsString(url);

    // Create a Blob and a download link
    const blob = new Blob([svgString], { type: 'image/svg+xml' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  async saveQrCodeAsPng(url: string, fileName: string = 'qrcode.png'): Promise<void> {
    const dataUrl = await this.generateQrCodeAsDataURL(url);

    // Create a download link
    const link = document.createElement('a');
    link.href = dataUrl;
    link.download = fileName;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
}
