import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'farsiDigit',
  standalone: true
})
export class FarsiDigitPipe implements PipeTransform {
  transform(value: string | number): string {
    const farsiDigits = ['', '', '', '', '', '', '', '', '', ''];

    // Convert the value to a string
    let strValue = value.toString();

    // Add thousand separators
    strValue = strValue.replace(/\B(?=(\d{3})+(?!\d))/g, '/');

    // Convert Arabic digits to Farsi digits
    return strValue
      .split('')
      .map((char) => (/\d/.test(char) ? farsiDigits[+char] : char))
      .join('');
  }
}