import { NgModule } from '@angular/core';
import { RouterModule, Routes, provideRouter, withHashLocation } from '@angular/router';
import { AppComponent } from './app.component';
import { AddnewuserComponent } from './addnewuser/addnewuser.component';
import { QuestionnaireComponent } from './questionnaire/questionnaire.component';
import { UserQuestionsComponent } from './user-questions/user-questions.component';
import { SeeQuestionsComponent } from './see-questions/see-questions.component';
import { OrderFormComponent } from './order-form/order-form.component';
import { AppQrCodeComponent } from './app-qr-code/app-qr-code.component';

export const routes: Routes = [
  {
    path: '',
    component: AppComponent,
    children: [
      { path: '', component: QuestionnaireComponent },
      { path: 'newuser', component: AddnewuserComponent },
      { path: 'userQuestion', component: UserQuestionsComponent },
      { path: 'seeQuestion', component:  SeeQuestionsComponent },
      { path: 'orderForm', component: OrderFormComponent },
      { path: 'qrCode', component: AppQrCodeComponent }
    ],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })], // Enable HashLocationStrategy
  exports: [RouterModule],
})
export class AppRoutingModule {}