import { Pipe, PipeTransform } from '@angular/core';
import moment from 'moment-jalaali';

@Pipe({
  name: 'jalaliDate',
  standalone: true
})
export class JalaliDatePipe implements PipeTransform {
  transform(value: string , format: string = 'jYYYY/jMM/jDD HH:mm'): string {
    if (!value) return '';
    moment.loadPersian({ usePersianDigits: false });

    const inputDate = value;
    
    // Extract year, month, day
    const [year, month, day] = inputDate.split('T')[0].split('-').map(Number);
    
    // Convert to a moment instance using Jalali format
    const jalaliMoment = moment(`${year}/${month}/${day}`, 'jYYYY/jMM/jDD').set({
      hour: 12, // Adjust the time if needed
      minute: 30
    });
    
    // Format it properly
    const formattedDate = jalaliMoment.format('jYYYY/jMM/jDD HH:mm');
    return formattedDate
  }
}
