import { Component , OnInit, Renderer2} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SectionComponent } from '../section/section.component';
import content from '../../assets/content.json';
import { ActivatedRoute, Router } from '@angular/router';
import {MatButtonModule} from '@angular/material/button';
import { GooglSheetService } from '../googl-sheet.service';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DialogMessageComponent } from '../shared/dialog-message/dialog-message.component';
import { MatDialog } from '@angular/material/dialog';
import { RouterModule, NavigationEnd } from '@angular/router';

type DataPoint = {
  min: number;
  value: number;
  max: number;
};

type SendInfoType = {
  user: string;
  timestamp: string;
  [key: string]: string | DataPoint[]; // Allow additional properties with arrays of DataPoint
};

@Component({
  selector: 'app-questionnaire',
  standalone: true,
  imports: [CommonModule, SectionComponent,MatButtonModule,MatProgressSpinnerModule,RouterModule],
  templateUrl: './questionnaire.component.html',
  styleUrl: './questionnaire.component.css'
})
export class QuestionnaireComponent {
  isLoading:boolean=false;
  data=content;
  outputs: any[] = [];
  userId: string | null = null;
  userType:string  = '';
  currentPath: string = '';
  summarizedSections: any[] = []; // Array to bind the second loop
  constructor(private router: Router,private route: ActivatedRoute,private renderer: Renderer2,private googleSheetService: GooglSheetService,private snackBar: MatSnackBar,private dialog: MatDialog) {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.currentPath = this.router.url.replace('/', ''); // Extract the current path
      }
    });    
  }  
  resultArray: any[] = [
    { probScenario: [] },
    { DeviationMostProbState: [] },
    { ForecastMacroVarForUpcomingPeriode: [] }
  ];
  get expiredDate():string {
    return localStorage.getItem('expiredDate') || "";
  }    
  onOutputArrayChange(output: any): void {
    if(this.data.expiredDate!=this.expiredDate){
      localStorage.setItem('expiredDate',this.data.expiredDate)
    }
    const index = this.outputs.findIndex((item) => item.link === output.link);
      if (index === -1) {
        this.outputs.push(output);
      } else {
        this.outputs[index] = output;
      }
  }
  ngOnInit() {
   this.route.queryParamMap.subscribe((params) => {
      this.userId = params.get('userId'); // Get the userId from the query params
      if (!this.userId) {
        console.warn('UserId is mandatory but not provided.');
      }
      this.userType = params.get('userType') || '';
      // if(this.userId && this.userId?.trim()==='newUser') {

      // }
    });
  } 
  currentTarget: HTMLElement | null = null;
  backLinkTop: string = '';
  backLinkLeft: string = '';
  backIsVisiBle:boolean=false;

  scrollToSection(linkId: string, isVisible:boolean): void {
    this.backIsVisiBle = isVisible;
    const element = document.getElementById(linkId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'center' });
      this.currentTarget = element;

      // Dynamically position the "Back" link beside the target
      const rect = element.getBoundingClientRect();
      this.backLinkTop = `${rect.top + window.scrollY}px`; // Account for scrolling
      this.backLinkLeft = `${rect.left + rect.width-40}px`; // Offset by 10px from the target's right edge
    }
    this.menuActive = false;
  }

  scrollToTop(): void {
    window.scrollTo({ top: 0, behavior: 'smooth' });
    this.currentTarget = null; // Hide the "Back" link
  }
  menuActive = false;

  toggleMenu() {
    this.menuActive = !this.menuActive;
  }
  closeMenu() {
    this.menuActive = false;
  }   
  async sendInfo(){
    let sendInfMostProb:SendInfoType= {
        user:'',
        timestamp:new Date().toLocaleString()
    };
    this.data.economicVar.forEach((item) => {
      sendInfMostProb[item.id] = []; // Initialize as an empty array
    });
    let sendInfDiv:SendInfoType= {
      user:'',
      timestamp:new Date().toLocaleString(),
    };
    this.data.economicVar.forEach((item) => {
      sendInfDiv[item.id] = []; // Initialize as an empty array
    });
    for(let item of this.outputs){
      const link = item.link.split('-');
      if(link[1]!='Diviation'){
        sendInfMostProb.user=this.userId?? '';
        const _column = [...item.column.slice(2, 5),
          ...[({value:(item.column[item.column.length-2].value+item.column[item.column.length-1].value)/2,
               min:(item.column[item.column.length-2].min+item.column[item.column.length-1].min)/2,
               max:(item.column[item.column.length-2].max+item.column[item.column.length-1].max)/2
              } as any)],
          ...item.column.slice(5, 6)];
        sendInfMostProb[link[0]]= [..._column];
      } else {
        sendInfDiv.user=this.userId?? '';
        const _column=[
          ...item.column.slice(1, 2),
          ...[
             {
              value:item.column[item.column.length-2].value*.7+item.column[item.column.length-1].value*.3,
              min:item.column[item.column.length-2].min*.7+item.column[item.column.length-1].min*.3,
              max:item.column[item.column.length-2].max*.7+item.column[item.column.length-1].max*.3
             },
             {
              value:item.column[item.column.length-2].value*.3+item.column[item.column.length-1].value*.7,
              min:item.column[item.column.length-2].min*.3+item.column[item.column.length-1].min*.7,
              max:item.column[item.column.length-2].max*.3+item.column[item.column.length-1].max*.7
             }
          ],
          ...item.column.slice(2, 3),
        ]
        sendInfDiv[link[0]]= [..._column];
      }
    }
    // const factor = {
    //   UncertaintyIndex:8.98,
    //   OilSale:25.76,
    //   OilExport:0.48,
    //   LiquidityGrowth:14.64,
    //   CurrencyGrowth: 167.802,
    //   RateOfReturnInTheDebtMarket:3.97
    // }
    let sendUncerAdjust:SendInfoType= {
      user:this.userId?? '',
      timestamp:new Date().toLocaleString()
    };    
    this.data.economicVar.forEach((item) => {
      sendUncerAdjust[item.id] = [
        {
          value: parseFloat(
            ((((sendInfMostProb[item.id][1] as DataPoint).max - (sendInfMostProb[item.id][1] as DataPoint).min) / 4) / item.uncerAdjust).toFixed(2)
          ),
          min: 0,
          max: 100,
        },
        {
          value: parseFloat(
            ((((sendInfMostProb[item.id][2] as DataPoint).max - (sendInfMostProb[item.id][2] as DataPoint).min) / 4) / item.uncerAdjust).toFixed(2)
          ),
          min: 0,
          max: 100,
        },
        {
          value: parseFloat(
            ((((sendInfMostProb[item.id][3] as DataPoint).max - (sendInfMostProb[item.id][3] as DataPoint).min) / 4) / item.uncerAdjust).toFixed(2)
          ),
          min: 0,
          max: 100,
        },
        {
          value: parseFloat(
            ((((sendInfMostProb[item.id][4] as DataPoint).max - (sendInfMostProb[item.id][4] as DataPoint).min) / 4) / item.uncerAdjust).toFixed(2)
          ),
          min: 0,
          max: 100,
        },
      ];
    });
    
    // UncertaintyIndex:[
    //   {value:((((sendInfMostProb['UncertaintyIndex'][1] as DataPoint).max-(sendInfMostProb['UncertaintyIndex'][1] as DataPoint).min)/4)/factor.UncertaintyIndex).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['UncertaintyIndex'][2] as DataPoint).max-(sendInfMostProb['UncertaintyIndex'][2] as DataPoint).min)/4)/factor.UncertaintyIndex).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['UncertaintyIndex'][3] as DataPoint).max-(sendInfMostProb['UncertaintyIndex'][3] as DataPoint).min)/4)/factor.UncertaintyIndex).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['UncertaintyIndex'][4] as DataPoint).max-(sendInfMostProb['UncertaintyIndex'][4] as DataPoint).min)/4)/factor.UncertaintyIndex).toFixed(2),min:0,max:100}
    // ],
    // OilSale:[
    //   {value:((((sendInfMostProb['OilSale'][1] as DataPoint).max-(sendInfMostProb['OilSale'][1] as DataPoint).min)/4)/factor.OilSale).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilSale'][2] as DataPoint).max-(sendInfMostProb['OilSale'][2] as DataPoint).min)/4)/factor.OilSale).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilSale'][3] as DataPoint).max-(sendInfMostProb['OilSale'][3] as DataPoint).min)/4)/factor.OilSale).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilSale'][4] as DataPoint).max-(sendInfMostProb['OilSale'][4] as DataPoint).min)/4)/factor.OilSale).toFixed(2),min:0,max:100}
    // ],
    // OilExport:[
    //   {value:((((sendInfMostProb['OilExport'][1] as DataPoint).max-(sendInfMostProb['OilExport'][1] as DataPoint).min)/4)/factor.OilExport).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilExport'][2] as DataPoint).max-(sendInfMostProb['OilExport'][2] as DataPoint).min)/4)/factor.OilExport).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilExport'][3] as DataPoint).max-(sendInfMostProb['OilExport'][3] as DataPoint).min)/4)/factor.OilExport).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['OilExport'][4] as DataPoint).max-(sendInfMostProb['OilExport'][4] as DataPoint).min)/4)/factor.OilExport).toFixed(2),min:0,max:100}
    // ],
    // LiquidityGrowth:[
    //   {value:((((sendInfMostProb['LiquidityGrowth'][1] as DataPoint).max-(sendInfMostProb['LiquidityGrowth'][1] as DataPoint).min)/4)/factor.LiquidityGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['LiquidityGrowth'][2] as DataPoint).max-(sendInfMostProb['LiquidityGrowth'][2] as DataPoint).min)/4)/factor.LiquidityGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['LiquidityGrowth'][3] as DataPoint).max-(sendInfMostProb['LiquidityGrowth'][3] as DataPoint).min)/4)/factor.LiquidityGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['LiquidityGrowth'][4] as DataPoint).max-(sendInfMostProb['LiquidityGrowth'][4] as DataPoint).min)/4)/factor.LiquidityGrowth).toFixed(2),min:0,max:100}
    // ],
    // CurrencyGrowth:[
    //   {value:((((sendInfMostProb['CurrencyGrowth'][1] as DataPoint).max-(sendInfMostProb['CurrencyGrowth'][1] as DataPoint).min)/4)/factor.CurrencyGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['CurrencyGrowth'][2] as DataPoint).max-(sendInfMostProb['CurrencyGrowth'][2] as DataPoint).min)/4)/factor.CurrencyGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['CurrencyGrowth'][3] as DataPoint).max-(sendInfMostProb['CurrencyGrowth'][3] as DataPoint).min)/4)/factor.CurrencyGrowth).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['CurrencyGrowth'][4] as DataPoint).max-(sendInfMostProb['CurrencyGrowth'][4] as DataPoint).min)/4)/factor.CurrencyGrowth).toFixed(2),min:0,max:100}
    // ],
    // RateOfReturnInTheDebtMarket:[
    //   {value:((((sendInfMostProb['RateOfReturnInTheDebtMarket'][1] as DataPoint).max-(sendInfMostProb['RateOfReturnInTheDebtMarket'][1] as DataPoint).min)/4)/factor.RateOfReturnInTheDebtMarket).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['RateOfReturnInTheDebtMarket'][2] as DataPoint).max-(sendInfMostProb['RateOfReturnInTheDebtMarket'][2] as DataPoint).min)/4)/factor.RateOfReturnInTheDebtMarket).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['RateOfReturnInTheDebtMarket'][3] as DataPoint).max-(sendInfMostProb['RateOfReturnInTheDebtMarket'][3] as DataPoint).min)/4)/factor.RateOfReturnInTheDebtMarket).toFixed(2),min:0,max:100},
    //   {value:((((sendInfMostProb['RateOfReturnInTheDebtMarket'][4] as DataPoint).max-(sendInfMostProb['RateOfReturnInTheDebtMarket'][4] as DataPoint).min)/4)/factor.RateOfReturnInTheDebtMarket).toFixed(2),min:0,max:100}
    // ]
    // console.log(sendInfDiv);
    // console.log(sendInfMostProb);
    // console.log(JSON.stringify(sendInfMostProb))
    const finalSend = {
      MostLikelyState:sendInfMostProb,
      RiskBalance:sendInfDiv,
      UncertAdjust:sendUncerAdjust
    }
    this.isLoading = true;
    try {
      const token = this.userType==='nonExpert'?
        'https://script.google.com/macros/s/AKfycbwINPcUjLLMyD_6Ijhy7HnrmoSGHoPq5QwoSoaHcg9jV69y7XrmjhAuAecY7-UfL0hldw/exec':
        'https://script.google.com/macros/s/AKfycbyQnMgQTLvy2UeUf85n1F5Fmt6AWqwphAI1m8hL1ahIghhop3v-IrK0bF7iwyIt6zWs/exec';
      await this.googleSheetService.postData(finalSend,token);
      this.dialog.open(DialogMessageComponent, {
        data: { message:'    ', isError:false },
        width: '300px',
      });
      this.isLoading=false;
 
    } catch (error: any) {
      this.dialog.open(DialogMessageComponent, {
        data: { message:'    .     !', isError:true },
        width: '300px'
      });
      this.isLoading=false;
    }    
  }
  get showSendButton():boolean{
    for(let item of this.outputs){
      if(!item.isReadyToSend){
        return false
      }
    }
    return true
  }
}
