import { Component } from '@angular/core';
import { FormControl, Validators,FormsModule,ReactiveFormsModule} from '@angular/forms';
import { GooglSheetService } from '../googl-sheet.service'
import { MatDialog } from '@angular/material/dialog';
import { DialogMessageComponent } from '../shared/dialog-message/dialog-message.component';
import {MatCardModule} from '@angular/material/card';
import {MatFormFieldModule} from '@angular/material/form-field';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { CommonModule } from '@angular/common';
import moment from 'moment-jalaali';
@Component({
  selector: 'app-user-questions',
  standalone: true,
  templateUrl: './user-questions.component.html',
  styleUrls: ['./user-questions.component.css'],
  imports: [FormsModule,MatCardModule,MatFormFieldModule,ReactiveFormsModule,MatProgressSpinnerModule,MatInputModule,MatButtonModule,CommonModule ]
})
export class UserQuestionsComponent {

  questionFromControl = new FormControl('', Validators.required);
  questionControl = new FormControl('', Validators.required);
  apiUrl = "YOUR_GOOGLE_APPS_SCRIPT_DEPLOYMENT_URL"; 
  isLoading = false; // Track request progress
  constructor(private googleSheetService: GooglSheetService,private dialog: MatDialog) {}
  get currentJalaliDateTime():string{
    moment.loadPersian({ usePersianDigits: false });
    const jalaliDate: string = moment().format('jYYYY/jMM/jDD HH:mm');
    return (jalaliDate);
  }
  async sendQuestion() {
    if (this.questionControl.valid &&  this.questionFromControl.valid) {
      try {
        this.isLoading=true;
        await this.googleSheetService.postData({'timestamp':this.currentJalaliDateTime,'question':`  : ${this.questionFromControl.value}\n: ${this.questionControl.value}`},'https://script.google.com/macros/s/AKfycbxlFEzPRk_Qk_g4VzISyywsFPrg4wfddZpMcffgExu_XeU2DCcFgZo8GGDhdc0ORbksXg/exec');
        this.dialog.open(DialogMessageComponent, {
          data: { message:'     ', isError:false },
          width: '300px',
        });
        this.questionControl.reset();
        this.isLoading=false;
   
      } catch (error: any) {
        this.dialog.open(DialogMessageComponent, {
          data: { message:'     .     !', isError:true },
          width: '300px'
        });
        this.isLoading=false;
      }
    }
  }       
}
